body.index {
	.splash_circle {
		background: #fff;
		width: 250px;
		height: 250px;
		border-radius: 100%;
		text-align: center;
		padding: 72px 10px 0;
		color: $main-green-dark;

		span {
			font-size: 13px;
			text-transform: uppercase;
			display: block;
			margin-bottom: 20px; }

		small {
			font-size: 20px;
			font-weight: $weight-semibold; }

		strong {
			font-size: 60px;
			font-weight: $weight-bold;
			display: block; } }

	header.header--landing {
		height: 100%;
		height: 100vh;
		position: relative;
		min-height: 760px;
		background: transparent;

		&.header--front-slide-1 {
			.header-nav {
				border-bottom: 1px solid rgba(255, 255, 255, .3);
				background: transparent;

				ul li a {
					color: #fff;

					&:hover {
						background: #fff;
						color: $main-green; } } }

			.hamburger .meat {
				background: #fff !important; } }

		&.header--front-slide-2 {
			.header_content {
				color: $main-green; }

			.header-nav {
				border-bottom: 1px solid rgba(255, 255, 255, .3);
				background: transparent;

				ul li a {
					color: $main-green-dark;

					&:hover {
						background: #fff;
						color: $main-green-dark; } } }

			.hamburger .meat {
				background: $main-green-dark !important; } }

		.header_content {
			color: #fff;
			margin-top: 20vh;
			padding-bottom: 60px;

			h1 {
				font-size: 46px;
				font-weight: $weight-bold;
				margin: 0 0 20px; }

			p {
				font-size: 17px;
				line-height: 1.8;
				margin: 0 0 50px; }

			a.button {
				color: $main-green-dark;
				font-size: 18px; }

			.headervideo-block {
				padding-left: 60px;

				span.icon {
					position: relative;
					cursor: pointer;
					margin-bottom: 30px;

					&:after {
						content: "";
						display: block;
						position: absolute;
						width: 188%;
						height: 150%;
						border-radius: 100%;
						left: -50%;
						top: -25%;
						border: 3px solid transparent;
						opacity: 1;
						transform: scale(1); }

					&:hover:after {
						border: 3px solid #fff;
						opacity: 0;
						transform: scale(1.4);
						transition: border 400ms, transform 400ms, opacity 400ms; } }

				strong {
					margin: 40px 0 20px;
					display: block;
					color: $main-green-dark;
					font-size: 18px;
					font-weight: $weight-semibold; } }

			.header_slides_container {
				overflow: hidden;

				.header_slides {
					width: 200%;
					max-width: 200%;
					transition: transform 300ms;
					transform: translateX(0%);

					.header_slide {
						width: 50%;
						float: left; } } }

			.header_slide_nav {
				font-size: 0;
				text-align: center;
				position: absolute;
				bottom: 30px;
				width: 100%;
				height: 30px;

				.header_slide_nav_item {
					background: $main-green-dark;
					border-radius: 100%;
					width: 30px;
					height: 30px;
					display: inline-block;
					margin: 0 5px;
					cursor: pointer;

					&:hover {
						background: darken($main-green-dark, 10%); }

					&.header_slide_nav_item--active {
						background: #fff;
						cursor: default; } } } }

		.header_bg_slides_container {
			overflow: hidden;
			position: absolute;
			z-index: -1;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;

			.header_bg_slides {
				width: 200%;
				max-width: 200%;
				height: 100%;
				transition: transform 300ms;
				transform: translateX(0%);

				.header_bg_item {
					width: 50%;
					float: left;
					height: 100%;

					&.header_bg_item--1 {
						background: $main-green url(/images/header_pattern.png) no-repeat right 134px; }

					&.header_bg_item--2 {
						background: #fff url(/images/header_bg.jpg) no-repeat center center;
						background-size: cover; } } } }

		.header_footer {
			background: rgba($dark-grey, 0.75);
			width: 100%;
			position: absolute;
			left: 0;
			bottom: 0;
			color: #fff;
			text-align: center;
			padding: 55px;
			font-size: 32px;

			&.header_footer--sticky {
				position: fixed;
				top: 0;
				bottom: auto;
				background: rgba($dark-grey, 0.95);
				z-index: 150;
				padding: 30px 15px;
				font-size: 26px; }

			a {
				color: #fff;
				text-decoration: underline; } } }

	.block {
		&.block--one {
			padding: 120px 0;

			h2 {
				font-size: 40px;
				color: $main-green-dark;
				margin: 0 0 20px; }

			p {
				font-size: 15px;
				line-height: 2;
				color: $greyish-brown;
				margin: 10px 0 0;

				&.sub_header {
					margin: 0 auto 60px;
					max-width: 500px; } }

			h3 {
				margin: 20px 0 10px;
				color: $main-green-dark;
				font-size: 22px;
				font-weight: normal; } }

		&.block--two {
			color: $white;
			background: url(/images/block-two-bg.jpg) no-repeat center center;
			background-size: cover;
			padding: 200px 0;

			h2 {
				font-weight: 600;
				font-size: 40px;
				line-height: 1.2;
				margin: 0 0 30px; }

			p {
				font-size: 15px;
				line-height: 1.5;
				color: $white;
				margin: 0 0 30px; } }

		&.block--three {
			padding: 150px 0;

			blockquote {
				margin: 0 0 70px;
				padding: 50px 50px 0;
				font-size: 24px;
				line-height: 1.5;
				color: $greyish-brown; }

			span.dietist-name {
				margin: 20px 0 0;
				display: block;
				color: $greyish-brown;
				font-size: 16px; } }

		&.block--four {
			background: $white;
			text-align: center;
			margin: 0 0 150px 0; }

		&.block--five {
			background: $almost-white;

			img {
				float: left;
				width: 30vw;
				height: 20vw; }

			p {
				max-width: 680px;
				font-size: 18px;
				line-height: 1.8;
				color: $greyish-brown;
				margin: 0 auto 20px; }

			.block_content {
				float: right;
				width: 70vw;
				height: 20vw;
				padding: 5vw;
				background: $almost-white; } }

		&.block--six {
			color: $white;
			padding: 190px 0;
			background: url(/images/block-six-bg.jpg) no-repeat center center;
			background-size: cover;

			h2 {
				font-weight: normal;
				font-size: 34px;
				margin: 0 0 30px; }

			p {
				line-height: 1.5;
				font-size: 15px;
				margin: 0 0 50px; } }

		&.block--seven {
			background: $almost-white;
			padding: 90px 0;

			h2 {
				font-weight: 700;
				font-size: 40px;
				color: $greyish-brown;
				margin: 0 0 10px; }

			h3 {
				font-size: 26px;
				color: $greyish-brown;
				margin: 0 0 20px;
				font-weight: normal; }

			p {
				font-size: 15px;
				line-height: 2;
				color: $greyish-brown;
				margin: 0 0 20px; } }

		&.block--eight {
			padding: 150px 0;

			img {
				margin-left: -20%; }

			h2 {
				font-weight: 700;
				font-size: 40px;
				color: $main-green-dark;
				margin: 30px 0 20px; }

			p {
				font-size: 15px;
				line-height: 2;
				color: $greyish-brown;
				margin: 0 0 20px; } } }

	.slider_container {
		overflow: hidden;
		position: relative;

		.slider-arrow-left,
		.slider-arrow-right {
			position: absolute;
			z-index: 1;
			cursor: pointer; }

		.slider-arrow-left {
			left: 50px;
			top: 50%;
			margin-top: -22px; }

		.slider-arrow-right {
			right: 50px;
			top: 50%;
			margin-top: -22px; }

		.slider {
			max-width: none;

			.slide_container {
				transition: -webkit-transform 450ms;
				transition: transform 450ms;

				.slide {
					overflow: hidden;
					position: relative;
					float: left;
					width: 100%;
					padding: 10px 0; } } } } }

@media all and (max-width: 1599px) {
	body.index {
		.block {
			&.block--five {
				.block_content {
					padding: 3vw; } } } } }

@media all and (max-width: 1299px) {
	body.index {
		header.header--landing {
			background-position: right -300px top 134px; }

		.block {
			&.block--five {
				.block_content {
					padding: 1vw; }

				p {
					line-height: 1.5; } } } } }

@media all and (max-width: 991px) {
	body.index {
		header.header--landing {
			background-position: right -500px top 134px; }

		.block {
			&.block--five {
				img,
				.block_content {
					float: none;
					height: auto;
					width: 100%; }

				.block_content {
					padding: 5vw; } }

			&.block--one {
				.block_item {
					h3 {
						line-height: 1.3; }

					p {
						line-height: 1.6;
						font-size: 14px; } } } } } }

@media all and (max-width: 767px) {
	body.index {
		.slider_container {
			.slider-arrow-right {
				right: 5px; }

			.slider-arrow-left {
				left: 5px; } }

		.block {
			&.block--one {
				.block_item {
					margin-bottom: 30px;

					&:last-of-type {
						margin-bottom: 0; } } }

			&.block--three {
				padding: 50px 0;

				blockquote {
					padding: 30px 30px 0;
					font-size: 20px; } }

			&.block--eight {
				padding: 50px 0;

				h2 {
					font-size: 26px; } }

			&.block--one {
				h2 {
					font-size: 32px; } }

			&.block--two {
				h2 {
					font-size: 32px; } }

			&.block--six {
				h2 {
					font-size: 26px; } }

			&.block--seven {
				h2 {
					font-size: 32px; }

				h3 {
					font-size: 22px; } }

			&.block--seven,
			&.block--six,
			&.block--two,
			&.block--one {
				padding: 50px 0; } }

		header.header--landing {
			height: auto;
			min-height: 0;
			padding: 0 0 50px;
			background-position-y: 96px;
			background-position: right -700px top 96px;

			a.button {
				border-radius: 3px; }

			.header_content {
				margin-top: 50px;

				h1 {
					font-size: 36px;
					padding-right: 100px; }

				.headervideo-block {
					padding-left: 30px; } } } } }

@media all and (max-width: 480px) {
	body.index {
		header.header--landing {
			background-position: right -750px top 96px;

			a.button {
				border-radius: 3px;
				font-size: 16px; }

			.header_content {
				margin-top: 50px;

				h1 {
					font-size: 30px;
					padding: 0; }

				.headervideo-block {
					padding: 0; } } } } }
